import Mock from "mockjs";
Mock.mock("mock/indexList", {
  "list|5": [
    //随机返回5项
    {
      pic: "@image('200x150', '#50B347', '#FFF', 'Mock.js')",    
      title:"@csentence(11, 15)",
      disc:"@csentence"   
    }
  ]
});

Mock.mock("mock/newsList", {
  "list|40": [
    //随机返回7项
    {
      pic: "@image('200x150', '#50B347', '#FFF', 'Mock.js')",    
      title:"@csentence(11, 15)",
      disp:"@csentence(15, 25)",   
      time:"@date",
    }
  ]
});



